import axiosInstance from '@/api/axios-instance';

export default {
  sendOrder(accessKey, data) {
    return axiosInstance
      .post(
        '/orders',
        {
          ...data,
        },
        {
          params: {
            userAccessKey: accessKey,
          },
        }
      )
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },

  loadOrder(accessKey, orderId) {
    return axiosInstance
      .get(`/orders/${orderId}`, {
        params: {
          userAccessKey: accessKey,
        },
      })
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },

  loadPaymentWay(deliveryTypeId) {
    return axiosInstance
      .get('/payments', {
        params: {
          deliveryTypeId: deliveryTypeId,
        },
      })
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },
};
