<template>
  <transition name="fade">
    <div class="content" v-if="pageIsLoaded">
      <div class="content__top">
        <BaseBreadcrumbs
          :arr-sections="[
            { routeName: 'cart', slug: '', title: 'Корзина' },
            { title: 'Заказ' },
          ]"
        />

        <div class="content__row">
          <h1 class="content__title">
            Заказ оформлен <span>№ {{ order.id }}</span>
          </h1>
        </div>
      </div>

      <section class="cart">
        <form
          v-if="orderIsLoaded"
          class="cart__form form"
          action="#"
          method="POST"
        >
          <div class="cart__field">
            <p class="cart__message">
              Благодарим за&nbsp;выбор нашего магазина. На&nbsp;Вашу почту
              придет письмо с&nbsp;деталями заказа. Наши менеджеры свяжутся
              с&nbsp;Вами в&nbsp;течение часа для уточнения деталей доставки.
            </p>

            <CartUserInfoList :user="order" />
          </div>

          <CartOrderList
            :items="order.basket.items"
            :deliveryPrice="order.deliveryType.price"
          />
        </form>

        <form v-else class="cart__form form" action="#" method="POST">
          <div class="cart__field">
            <p class="cart__message">Заказ не найден</p>
          </div>
        </form>
      </section>
    </div>
  </transition>
</template>

<script>
import apiOrder from '@/api/order';

import BaseBreadcrumbs from '@/components/base/BaseBreadcrumbs.vue';
import CartUserInfoList from '@/components/cart/CartUserInfoList.vue';
import CartOrderList from '@/components/cart/CartOrderList.vue';
import pageLoadingProcessMixin from '@/mixins/page-loading-process';

import { mapState } from 'vuex';

export default {
  components: { BaseBreadcrumbs, CartUserInfoList, CartOrderList },
  mixins: [pageLoadingProcessMixin],
  data() {
    return {
      order: {},
      orderIsLoaded: true,
      // orderId: 2679,
    };
  },
  computed: {
    ...mapState({
      user: 'user',
      orderId: 'currentOrderId',
    }),
  },
  created() {
    this.loadOrder();
  },
  methods: {
    loadOrder() {
      this.updatePageIsLoaded(false);
      this.orderIsLoaded = false;

      apiOrder
        .loadOrder(this.user.accessKey, this.orderId)
        .then((response) => {
          this.order = response;
          this.$store.commit('updateCart', []);
          this.orderIsLoaded = true;
        })
        .catch((error) => {
          (this.user = {}), (this.orderId = ''), console.log(error);
          this.orderIsLoaded = false;

          if (error.code === 'ERR_NETWORK') {
            this.$store.commit('updateMessage', {
              text: 'Отсутствует соединение с интернетом',
            });
          } else {
            throw error;
          }
        })
        .finally(() => this.updatePageIsLoaded(true));
    },
  },
};
</script>
