import { mapMutations, mapState } from 'vuex';

const loadingProcessMixin = {
  computed: {
    ...mapState([
      'pageIsLoaded',
      'productsIsLoading',
      'productsIsLoaded',
      'productsLoadFault',
    ]),
  },
  methods: {
    ...mapMutations([
      'updatePageIsLoaded',
      'updateProductIsLoading',
      'updateProductsIsLoaded',
      'updateProductsLoadFault',
    ]),

    setProductLoadingVars(isLoading, isLoaded, loadFault) {
      this.updateProductIsLoading(isLoading);
      this.updateProductsIsLoaded(isLoaded);
      this.updateProductsLoadFault(loadFault);
    },
  },
};

export default loadingProcessMixin;
