<template>
  <transition name="fade">
    <div class="content" v-if="pageIsLoaded">
      <div class="content__top">
        <div class="content__row">
          <h1 class="content__title">Каталог</h1>
          <span class="content__info">
            {{ productsAmount }} {{ productsAmountEnding }}
          </span>
        </div>
      </div>

      <div class="content__catalog content__catalog--main-page">
        <ProductFilter
          :product-colors="productColors"
          :product-materials="productMaterials"
          :product-collections="productCollections"
          :product-categories="productCategories"
          :is-loading="productsIsLoading"
          :is-loaded="productsIsLoaded"
          v-model:form-color-id="form.colorIds"
          v-model:form-material-id="form.materialIds"
          v-model:form-collection-id="form.seasonIds"
          v-model:form-category-id="form.categoryId"
          v-model:form-price-from="form.minPrice"
          v-model:form-price-to="form.maxPrice"
          @submit-form-filter="loadProducts"
        />

        <div class="pagination-control">
          <label class="pagination-control__label form__label--select">
            <span class="pagination-control__title">Товаров на странице</span>
            <BaseSelect
              :isZeroOptionShow="false"
              :list="arrLimitItemsOnPage"
              v-model="form.limit"
              @change="loadProducts"
            />
          </label>
        </div>

        <SectionCatalog
          :products-is-loading="productsIsLoading"
          :products-load-fault="productsLoadFault"
          :products-is-loaded="productsIsLoaded"
          :products="products"
          @paginate="loadProducts"
        />
      </div>
    </div>
  </transition>
</template>

<script>
import apiCatalog from '@/api/catalog';

import loadingProcessMixin from '@/mixins/loading-process';
import paginationConnectionMixin from '@/mixins/pagination-connection';

import setProductsAmountEnding from '@/helpers/set-product-amount-ending';
import formatObjectOfStrings from '@/helpers/format-object-of-strings';

import ProductFilter from '@/components/product/ProductFilter.vue';
import SectionCatalog from '@/components/sections/SectionCatalog.vue';
import BaseSelect from '@/components/base/BaseSelect.vue';

export default {
  components: {
    ProductFilter,
    SectionCatalog,
    BaseSelect,
  },
  mixins: [loadingProcessMixin, paginationConnectionMixin],

  data() {
    return {
      products: [],

      productColors: [],
      productMaterials: [],
      productCollections: [],
      productCategories: [],

      form: {
        colorIds: [],
        materialIds: [],
        seasonIds: [],

        categoryId: 0,

        minPrice: 0,
        maxPrice: 0,

        page: 1,
        limit: 12,
      },

      pageCurrent: 1,
      pagesAmount: 0,
      arrLimitItemsOnPage: [
        { id: 12, title: 12 },
        { id: 24, title: 24 },
        { id: 32, title: 32 },
      ],

      productsAmount: 0,
    };
  },
  computed: {
    productsAmountEnding() {
      return setProductsAmountEnding(this.productsAmount);
    },
  },
  created() {
    this.loaderPage([
      this.loadProducts(),
      this.loadColors(),
      this.loadMaterials(),
      this.loadCollections(),
      this.loadCategories(),
    ]);
  },
  methods: {
    loaderPage(arrRequests) {
      this.updatePageIsLoaded(false);

      Promise.all(arrRequests).then(() => {
        this.updatePageIsLoaded(true);
      });
    },

    loadProducts() {
      this.form.page = this.pageCurrent;

      this.setProductLoadingVars(true, false, false);

      apiCatalog
        .loadProducts(this.form)
        .then((response) => {
          this.products = response.items;
          this.productsAmount = response.pagination.total;
          this.pagesAmount = response.pagination.pages;
          this.productAmountEnding = setProductsAmountEnding(
            this.productsAmount
          );
          this.setProductLoadingVars(false, true, false);
        })
        .catch((error) => {
          this.products = [];
          this.productsAmount = 0;
          this.pagesAmount = 0;
          this.productAmountEnding = 'товаров';

          this.setProductLoadingVars(false, false, true);

          if (error.code === 'ERR_NETWORK') {
            this.$store.commit('updateMessage', {
              text: 'Отсутствует соединение с интернетом',
            });
          } else {
            throw error;
          }
        });
    },

    loadColors() {
      apiCatalog
        .loadColors()

        .then((response) => {
          this.productColors = formatObjectOfStrings(response.items, 'title');
        })
        .catch((error) => {
          this.productColors = [];

          if (error.code === 'ERR_NETWORK') {
            this.$store.commit('updateMessage', {
              text: 'Отсутствует соединение с интернетом',
            });
          } else {
            throw error;
          }
        });
    },

    loadMaterials() {
      apiCatalog
        .loadMaterials()

        .then((response) => {
          this.productMaterials = response.items;
        })
        .catch((error) => {
          this.productMaterials = [];

          if (error.code === 'ERR_NETWORK') {
            this.$store.commit('updateMessage', {
              text: 'Отсутствует соединение с интернетом',
            });
          } else {
            throw error;
          }
        });
    },

    loadCollections() {
      apiCatalog
        .loadCollections()

        .then((response) => {
          this.productCollections = response.items;
        })
        .catch((error) => {
          this.productCollections = [];

          if (error.code === 'ERR_NETWORK') {
            this.$store.commit('updateMessage', {
              text: 'Отсутствует соединение с интернетом',
            });
          } else {
            throw error;
          }
        });
    },

    loadCategories() {
      apiCatalog
        .loadCategories()
        .then((response) => {
          this.productCategories = formatObjectOfStrings(
            response.items,
            'title'
          );
        })
        .catch((error) => {
          this.productCategories = [];

          if (error.code === 'ERR_NETWORK') {
            this.$store.commit('updateMessage', {
              text: 'Отсутствует соединение с интернетом',
            });
          } else {
            throw error;
          }
        });
    },
  },
};
</script>

<style>
.content__catalog--main-page {
  grid-template-rows: 52px auto;
}
.pagination-control {
  display: flex;
  justify-content: flex-end;
}
.pagination-control .form__select {
  padding-top: 10px;
  padding-bottom: 10px;
  min-width: 100px;
}

.pagination-control__label {
  position: relative;
  display: flex;
  flex-direction: column;
  row-gap: 5px;
  color: #222;
}

.pagination-control__title {
  position: absolute;
  top: -24px;
  right: 0;
  color: #737373;
  white-space: nowrap;
}
</style>
