<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img :src="srcImage" width="120" height="120" alt="Название товара" />
    </div>
    <h3 class="product__title">{{ product.product.title }}</h3>
    <p class="product__info product__info--color">
      Цвет:
      <span>
        <i :style="{ 'background-color': product.color.color.code }"></i>
        {{ product.color.color.title }}
      </span>
    </p>
    <p class="product__info product__info--size">
      Размер:
      <span>
        {{ product.size.title }}
      </span>
    </p>
    <span class="product__code"> Артикул: {{ product.product.id }} </span>

    <BaseInputAmount v-model:item-amount="productAmount" />

    <b class="product__price"> {{ renderPrice }} </b>
    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click="$emit('deleteProduct', product.id)"
    >
      <svg width="20" height="20" fill="currentColor">
        <path d="M17 1L1 17m16 0L1 1"></path>
      </svg>
    </button>
  </li>
</template>

<script>
import formatPrice from '@/helpers/format-price';
import BaseInputAmount from '../base/BaseInputAmount.vue';

import { mapState } from 'vuex';

export default {
  components: { BaseInputAmount },
  props: ['product'],
  emits: ['deleteProduct', 'changeProductAmount'],
  data() {
    return {
      productAmount: Number(this.product.quantity),
    };
  },
  computed: {
    ...mapState({
      user: 'user',
    }),
    renderPrice() {
      return formatPrice(this.productPriceTotal());
    },
    srcImage() {
      if (this.product.color?.gallery) {
        return this.product.color.gallery[0].file.url;
      } else {
        return './img/product-img-temporary.jpg';
      }
    },
  },
  watch: {
    productAmount() {
      this.$emit('changeProductAmount', {
        amount: this.productAmount,
        id: this.product.id,
      });
    },
  },
  methods: {
    productPriceTotal() {
      return this.product.price * this.productAmount;
    },
  },
};
</script>

<style>
.cart__form--cart-page .product__pic {
  grid-row: 1/5;
}
.cart__form--cart-page .product__info--size {
  grid-row: 3/4;
}

.cart__form--cart-page .product__code {
  grid-row: 4/5;
}

.cart__form--cart-page .form__counter {
  margin-bottom: 0;
  height: unset;
}

.cart__form--cart-page .form__counter .input-amount {
  padding: 0 5px;
  height: 20px;
}

.cart__form--cart-page .product__del {
  cursor: pointer;
}

.cart__form--cart-page .product__del svg {
  stroke: #9d9d9d;
  stroke-width: 0.8;
  transition: stroke 0.3s ease;
}

.cart__form--cart-page .product__del:focus svg,
.cart__form--cart-page .product__del:hover svg {
  stroke: #e02d71;
  stroke-width: 1;
}
</style>
