<template>
  <transition name="fade">
    <div class="content" v-if="pageIsLoaded">
      <div class="content__top">
        <BaseBreadcrumbs
          :arr-sections="[
            { routeName: 'cart', slug: '', title: 'Корзина' },
            { title: 'Оформление заказа' },
          ]"
        />

        <div class="content__row">
          <h1 class="content__title">Оформление заказа</h1>
        </div>
      </div>

      <section class="cart">
        <form
          class="cart__form form"
          method="POST"
          @submit.prevent="submitFormOrder"
        >
          <div class="cart__field">
            <div class="cart__data">
              <BaseFormFieldInput
                placeholder="Введите ваше полное имя"
                title="ФИО"
                v-model:error="formErrors.name"
                v-model="formData.name"
                @focus="resetErrorWarn"
              />

              <BaseFormFieldInput
                placeholder="Введите ваш адрес"
                title="Адрес доставки"
                v-model:error="formErrors.address"
                v-model="formData.address"
                @focus="resetErrorWarn"
              />

              <BaseFormFieldInput
                type="tel"
                placeholder="Введите ваш телефон"
                title="Телефон"
                v-model:error="formErrors.phone"
                v-model="formData.phone"
                @focus="resetErrorWarn"
              />

              <BaseFormFieldInput
                type="email"
                placeholder="Введи ваш Email"
                title="Email"
                v-model:error="formErrors.email"
                v-model="formData.email"
                @focus="resetErrorWarn"
              />

              <BaseFormFieldTextAria
                placeholder="Ваши пожелания"
                title="Комментарий к заказу"
                v-model:error="formErrors.comments"
                v-model="formData.comments"
                @focus="resetErrorWarn"
              />
            </div>

            <div class="cart__options">
              <h3 class="cart__title">Доставка</h3>
              <ul class="cart__options options">
                <li class="options__item">
                  <BaseCheckboxOrder
                    :price="0"
                    title="Самовывоз"
                    name="delivery"
                    :checkbox-value="1"
                    :modelValue="formData.deliveryTypeId"
                    @update:modelValue="updateDelivery"
                  />
                </li>
                <li class="options__item">
                  <BaseCheckboxOrder
                    :price="1200"
                    title="Курьером"
                    name="delivery"
                    :checkbox-value="2"
                    :modelValue="formData.deliveryTypeId"
                    @update:modelValue="updateDelivery"
                  />
                </li>
              </ul>

              <h3 class="cart__title">Оплата</h3>
              <ul class="cart__options options" v-if="paymentListIsLoaded">
                <li
                  class="options__item"
                  v-for="(item, index) in paymentWays"
                  :key="index"
                >
                  <BaseCheckboxOrder
                    :price="null"
                    :title="item.title"
                    name="pay"
                    :checkbox-value="item.id"
                    :modelValue="formData.paymentTypeId"
                    @update:modelValue="updatePaymentTypeId"
                  />
                </li>
              </ul>
            </div>
          </div>

          <CartOrderList :items="cart" :delivery-price="deliveryPrice">
            <button class="cart__button button button--primery" type="submit">
              Оформить заказ
            </button>
          </CartOrderList>

          <div class="cart__error form__error-block" v-if="errorWarnIsShow">
            <h4>Заявка не отправлена!</h4>
            <p>
              {{ errorMessage }}
            </p>
          </div>
        </form>
      </section>
    </div>
  </transition>
</template>

<script>
import apiOrder from '@/api/order';

import BaseBreadcrumbs from '@/components/base/BaseBreadcrumbs.vue';
import BaseFormFieldInput from '@/components/base/BaseFormFieldInput.vue';
import BaseFormFieldTextAria from '@/components/base/BaseFormFieldTextAria.vue';
import BaseCheckboxOrder from '@/components/base/BaseCheckboxOrder.vue';
import CartOrderList from '@/components/cart/CartOrderList.vue';
import pageLoadingProcessMixin from '@/mixins/page-loading-process';

import { mapState, mapMutations } from 'vuex';

export default {
  components: {
    BaseBreadcrumbs,
    BaseFormFieldInput,
    BaseFormFieldTextAria,
    BaseCheckboxOrder,
    CartOrderList,
  },
  mixins: [pageLoadingProcessMixin],

  data() {
    return {
      paymentWays: [],
      paymentListIsLoaded: true,
      deliveryPrice: 0,

      formData: {
        deliveryTypeId: 1,
        paymentTypeId: 1,
      },

      formErrors: {},
      errorCatched: false,
      errorMessage: '',
    };
  },

  computed: {
    ...mapState(['user', 'cart']),
    errorWarnIsShow() {
      return this.errorCatched;
    },
  },

  created() {
    this.updatePageIsLoaded(false);
    this.paymentListIsLoaded = false;

    apiOrder
      .loadPaymentWay(1)
      .then((response) => {
        this.updatePaymentTypeId({ value: 2 });
        this.paymentWays = response.reverse();
      })
      .catch((error) => {
        this.paymentWays = {};

        if (error.code === 'ERR_NETWORK') {
          this.$store.commit('updateMessage', {
            text: 'Отсутствует соединение с интернетом',
          });
        } else {
          throw error;
        }
      })
      .finally(() => {
        this.paymentListIsLoaded = true;
        this.updatePageIsLoaded(true);
      });
  },

  methods: {
    ...mapMutations(['updateCurrentOrderId']),

    resetErrorWarn() {
      this.errorCatched = false;
    },

    submitFormOrder() {
      apiOrder
        .sendOrder(this.user.accessKey, this.formData)
        .then((response) => {
          this.updateCurrentOrderId(response.id);
          this.$router.push('/cart/order-in-progress');
        })
        .catch((error) => {
          switch (error.code) {
            case 'ERR_NETWORK':
              this.updateMessage({
                text: 'Отсутствует соединение с интернетом',
              });
              break;
            case 'ERR_BAD_REQUEST':
              Object.assign(this.formErrors, error.response.data.error.request);
              this.errorCatched = true;
              this.errorMessage =
                'Пожалуйста, заполните отмеченные поля в форме';
              break;
            default:
              throw error;
          }
        });
    },

    updateDelivery({ value, price }) {
      this.formData.deliveryTypeId = value;
      this.deliveryPrice = price;
      this.loadPaymentWay(value);
    },

    updatePaymentTypeId({ value }) {
      this.formData.paymentTypeId = value;
    },

    loadPaymentWay(deliveryTypeId) {
      apiOrder
        .loadPaymentWay(deliveryTypeId)
        .then((response) => {
          this.updatePaymentTypeId({ value: 2 });
          this.paymentWays = response.reverse();
        })
        .catch((error) => {
          console.log(error);
          this.paymentWays = {};
        });
    },
  },
};
</script>
