import { createStore } from 'vuex';

export default createStore({
  state: {
    user: {},
    cart: [],
    message: {
      title: 'Сообщение',
      text: '',
    },
    currentOrderId: null,

    pageIsLoaded: true,

    productsIsLoading: false,
    productsIsLoaded: true,
    productsLoadFault: false,
  },

  mutations: {
    setUser(state, user) {
      state.user = Object.assign({}, user);
    },

    updateCart(state, cart) {
      state.cart = cart.splice(0, cart.length);
    },

    updateMessage(state, { title = 'сообщение', text }) {
      state.message.title = title;
      state.message.text = text;
    },

    updateCurrentOrderId(state, id) {
      state.currentOrderId = id;
    },

    updatePageIsLoaded(state, isLoaded) {
      state.pageIsLoaded = isLoaded;
    },

    updateProductIsLoading(state, isLoading) {
      state.productsIsLoading = isLoading;
    },

    updateProductsIsLoaded(state, isLoaded) {
      state.productsIsLoaded = isLoaded;
    },

    updateProductsLoadFault(state, isFault) {
      state.productsLoadFault = isFault;
    },
  },

  getters: {
    cartItemAmount(state) {
      return state.cart.length > 0 ? state.cart.length : 0;
    },

    cartPriceTotal(state) {
      return state.cart.reduce((total, product) => {
        return total + product.price * product.quantity;
      }, 0);
    },
  },
});
