<template>
  <transition name="fade">
    <div class="content" v-if="pageIsLoaded">
      <div class="content__top">
        <BaseBreadcrumbs :arr-sections="[{ title: 'Корзина' }]" />
        <div class="content__row">
          <h1 class="content__title">Корзина</h1>
          <CartItemAmountInTitle />
        </div>
      </div>

      <section class="cart">
        <form
          class="cart__form cart__form--cart-page form"
          action="#"
          method="POST"
        >
          <div class="cart__field">
            <ul class="cart__list">
              <CartItem
                v-for="(product, index) in cart"
                :key="index"
                :product="product"
                @delete-product="deleteProduct"
                @change-product-amount="changeProductAmount"
              />
            </ul>
          </div>

          <div class="cart__block">
            <p class="cart__desc" v-if="cart.length > 0">
              Мы&nbsp;посчитаем стоимость доставки на&nbsp;следующем этапе
            </p>
            <p class="cart__price">
              Итого: <span> {{ cartPriceTotal }}</span>
            </p>

            <button
              v-if="cart.length > 0"
              class="cart__button button button--primery"
              @click.prevent="goToOrderMaking"
            >
              Оформить заказ
            </button>
          </div>
        </form>
      </section>
    </div>
  </transition>
</template>

<script>
import apiCart from '@/api/cart';

import formatPrice from '@/helpers/format-price';

import pageLoadingProcessMixin from '@/mixins/loading-process';

import BaseBreadcrumbs from '@/components/base/BaseBreadcrumbs.vue';

import CartItem from '@/components/cart/CartItem.vue';
import CartItemAmountInTitle from '@/components/cart/CartItemAmountInTitle.vue';

import { mapState } from 'vuex';

export default {
  components: {
    BaseBreadcrumbs,
    CartItem,
    CartItemAmountInTitle,
  },
  mixins: [pageLoadingProcessMixin],
  computed: {
    ...mapState(['user', 'cart']),

    cartPriceTotal() {
      const cartPriceTotal = this.$store.getters.cartPriceTotal;
      return formatPrice(cartPriceTotal);
    },
  },

  beforeCreate() {
    this.$store.commit('updatePageIsLoaded', false);
  },

  created() {
    setTimeout(() => {
      this.updatePageIsLoaded(true);
    }, 1);
  },
  methods: {
    deleteProduct(productId) {
      apiCart
        .deleteProduct(this.user.accessKey, productId)
        .then((response) => {
          this.$store.commit('updateCart', response.items);
          this.cart = response.data.items;
        })
        .catch((error) => {
          console.log(error);
          return error;
        });
    },

    changeProductAmount({ amount, id }) {
      if (amount === '') {
        console.log('пустое значение количества товара');
        return false;
      }
      apiCart
        .changeProductAmount(this.user.accessKey, { amount, id })
        .then((response) => {
          console.log(response.items);
          this.$store.commit('updateCart', response.items);
        })
        .catch((error) => {
          console.log(error);
        });
    },

    goToOrderMaking() {
      this.$router.push('/cart/order-making');
    },
  },
};
</script>
