<template>
  <transition name="fade">
    <div class="message" v-if="isShow">
      <h3 class="message__title">{{ message.title }}</h3>
      <p class="message__text">{{ message.text }}</p>
    </div>
  </transition>
</template>

<script>
import { mapState } from 'vuex';
export default {
  computed: {
    ...mapState(['message']),
    isShow: {
      get() {
        return this.message.text !== '';
      },
      set(value) {
        this.$store.commit('updateMessage', { text: value });
      },
    },
  },
  watch: {
    message: {
      deep: true,
      handler: function () {
        if (this.message.text !== '') this.deleteMessage();
      },
    },
  },
  methods: {
    deleteMessage() {
      setTimeout(() => {
        this.isShow = '';
      }, 3000);
    },
  },
};
</script>

<style>
.message {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 15px 30px;
  min-width: 300px;

  border-radius: 5px;
  z-index: 100;
}

.message::after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: #efefef;
  opacity: 0.5;
  z-index: -1;
}

.message__title {
  margin: 0 0 20px;
}

.message__text {
  margin: 0;
}
</style>
