import axiosInstance from './axios-instance';

export default {
  loadProduct(productId) {
    return axiosInstance
      .get(`/products/${productId}`)
      .then((respose) => {
        return respose.data;
      })
      .catch((error) => {
        throw error;
      });
  },

  loadProducts(data) {
    return axiosInstance
      .get('/products', {
        params: {
          ...data,
        },
      })
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },

  loadColors() {
    return axiosInstance
      .get('/colors')
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },

  loadMaterials() {
    return axiosInstance
      .get('/materials')
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },

  loadCollections() {
    return axiosInstance
      .get('/seasons')
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },

  loadCategories() {
    return axiosInstance
      .get('/productCategories')
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },
};
