<template>
  <transition name="fade">
    <div class="content" v-if="pageIsLoaded">
      <div class="content__top">
        <BaseBreadcrumbs :arr-sections="[category, product]" />
      </div>

      <section class="item">
        <div class="item__pics pics">
          <div class="pics__wrapper">
            <img width="570" height="570" :src="imgSrc" :alt="product.title" />
          </div>
        </div>

        <div class="item__info">
          <span class="item__code">Артикул: {{ product.id }}</span>
          <h2 class="item__title">{{ product.title }}</h2>

          <div class="item__form">
            <form
              class="form"
              method="POST"
              @submit.prevent="sendProduct(form)"
            >
              <div class="item__row item__row--center">
                <BaseInputAmount v-model:item-amount="form.quantity" />

                <b class="item__price"> {{ productPriceTotal }} </b>
              </div>

              <div class="item__row">
                <fieldset class="form__block">
                  <legend class="form__legend">Цвет</legend>
                  <ul class="colors colors--black">
                    <ProductItemColor
                      v-for="(colorObj, index) in product.colors"
                      input-name="colorId"
                      :key="index"
                      :colorObjIndex="index"
                      :color-obj="colorObj"
                      v-model="form.colorId"
                      @picked="setImgSrc"
                    />
                  </ul>
                </fieldset>

                <fieldset class="form__block">
                  <legend class="form__legend">Размер</legend>
                  <label
                    class="form__label form__label--small form__label--select"
                  >
                    <BaseSelect
                      selectName="sizeId"
                      :list="product.sizes"
                      :isZeroOptionShow="false"
                      v-model="form.sizeId"
                    />
                  </label>
                </fieldset>
              </div>

              <BaseButton
                :action-is-done="isProductAddToCart"
                :loading="isProductSending"
              />
            </form>
          </div>
        </div>

        <div class="item__desc">
          <ProductTabs v-model="currentTabIndex" />

          <div class="item__content" v-show="currentTabIndex === 0">
            <h3>Состав:</h3>
            <p>
              <span v-for="(material, index) in product.materials" :key="index">
                <span>{{ material.title }}</span>
                <br />
              </span>
            </p>

            <h3>Уход:</h3>
            <p>
              {{ productDescription }}
            </p>
          </div>

          <div class="item__content" v-show="currentTabIndex === 1">
            <h3>Доставка</h3>
            <p></p>
            <h3>Возврат</h3>
            <p></p>
          </div>
        </div>
      </section>
    </div>
  </transition>
</template>

<script>
import apiCart from '@/api/cart';
import apiCatalog from '@/api/catalog';

import formatPrice from '@/helpers/format-price';

import setImgSrcMixin from '@/mixins/set-img-src';
import loadingProcessMixin from '@/mixins/loading-process';

import { mapState, mapMutations } from 'vuex';

import BaseBreadcrumbs from '@/components/base/BaseBreadcrumbs.vue';

import BaseSelect from '@/components/base/BaseSelect.vue';
import ProductItemColor from '@/components/product/ProductItemColor.vue';
import BaseInputAmount from '@/components/base/BaseInputAmount.vue';
import ProductTabs from '@/components/product/ProductTabs.vue';
import BaseButton from '@/components/base/BaseButton.vue';

export default {
  components: {
    BaseBreadcrumbs,
    ProductItemColor,
    BaseSelect,
    BaseInputAmount,
    ProductTabs,
    BaseButton,
  },

  mixins: [setImgSrcMixin, loadingProcessMixin],

  data() {
    return {
      product: {},
      category: {},

      form: {
        colorId: null,
        sizeId: null,
        quantity: 1,
        productId: null,
      },

      imgSrc: null,
      currentTabIndex: 0,

      isProductAddToCart: false,
      isProductSending: false,
    };
  },

  computed: {
    ...mapState({
      user: 'user',
    }),

    productDescription() {
      return this.product?.content === ''
        ? 'Описание продукта не нашли'
        : this.product.content;
    },

    productPriceTotal() {
      return formatPrice(this.form.quantity * this.product.price);
    },
  },

  watch: {
    form: {
      deep: true,
      handler: function () {
        this.isProductAddToCart = false;
      },
    },
  },

  created() {
    this.loadProduct(this.$route.params.id);
  },

  methods: {
    ...mapMutations(['updateMessage']),

    loadProduct(productId) {
      this.updatePageIsLoaded(false);
      this.setProductLoadingVars(true, false, false);

      apiCatalog
        .loadProduct(productId)
        .then((respose) => {
          this.product = respose;
          this.product.routeName = 'product';

          this.category = respose.category;
          this.category.routeName = 'category';

          this.form.colorId = respose.colors[0].color.id;
          this.form.sizeId = respose.sizes[0].id;
          this.form.productId = this.product.id;
          this.setImgSrc(0);

          this.setProductLoadingVars(false, true, false);
        })
        .catch((error) => {
          this.product = {};
          this.setProductLoadingVars(false, false, true);

          if (error.code === 'ERR_NETWORK') {
            this.$store.commit('updateMessage', {
              text: 'Отсутствует соединение с интернетом',
            });
          } else {
            throw error;
          }
        })
        .finally(() => this.updatePageIsLoaded(true));
    },

    sendProduct(request) {
      this.isProductAddToCart = false;
      this.isProductSending = true;

      apiCart
        .sendProduct(this.user.accessKey, request)
        .then((response) => {
          this.$store.commit('updateCart', response.items);
          this.isProductAddToCart = true;
        })
        .catch((error) => {
          switch (error.code) {
            case 'ERR_NETWORK':
              this.updateMessage({
                text: 'Отсутствует соединение с интернетом',
              });
              break;
            case 'ERR_BAD_REQUEST':
              this.iterateResponseMessages(error);
              break;
            default:
              throw error;
          }
        })
        .finally(() => (this.isProductSending = false));
    },

    iterateResponseMessages(error) {
      const messages = error.response.data.error.request;
      for (let key in messages) {
        this.updateMessage({ text: messages[key] });
      }
    },
  },
};
</script>
