<template>
  <div class="wrapper-app">
    <SiteHeader />

    <main class="container wrapper-main">
      <div
        style="height: 100%"
        class="content"
        v-if="(productsIsLoading || productsLoadFault) && !isCurrentPageMain"
      >
        <BasePreloaderSmall
          :productsIsLoading="productsIsLoading"
          :productsLoadFault="productsLoadFault"
        />
      </div>

      <BaseMessage />

      <router-view />
    </main>
    <SiteFooter v-if="pageIsLoaded" />
  </div>
</template>

<script>
import cart from '@/api/cart';
import {
  getUserFromLocalStorage,
  setUserToLocalStorage,
} from '@/helpers/local-storage-connection';
import pageLoadingProcessMixin from './mixins/page-loading-process';

import { mapMutations } from 'vuex';

import SiteHeader from '@/components/site/SiteHeader.vue';
import SiteFooter from '@/components/site/SiteFooter.vue';
import BasePreloaderSmall from '@/components/base/BasePreloaderSmall.vue';
import BaseMessage from '@/components/base/BaseMessage.vue';

export default (await import('vue')).defineComponent({
  components: {
    SiteHeader,
    SiteFooter,
    BasePreloaderSmall,
    BaseMessage,
  },
  mixins: [pageLoadingProcessMixin],

  created() {
    this.setUser();
  },
  computed: {
    isCurrentPageMain() {
      return this.$route.name === 'main';
    },
  },
  methods: {
    ...mapMutations({
      setUserToState: 'setUser',
      updateCartItemAmount: 'updateCartItemAmount',
      updateCart: 'updateCart',
    }),
    async setUser() {
      const user = getUserFromLocalStorage();

      if (user) {
        this.updatePageIsLoaded(false);
        const data = await cart.getCart(user.accessKey);
        this.setUserToState(user);
        this.updateCart(data.items);
        this.updatePageIsLoaded(true);
      } else {
        const data = await cart.getCart();
        setUserToLocalStorage(data.user);
        this.setUserToState(data.user);
      }
    },
  },
});
</script>

<style>
.wrapper-app {
  display: flex;
  flex-direction: column;
  height: 100vh;
}

.wrapper-main {
  position: relative;
  flex: 1 0 auto;
}

.fade-enter-active,
.fade-leave-active {
  opacity: 1;
  transition: opacity 1s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

.bounce-enter-active {
  animation: bounce-in 0.5s;
}
.bounce-leave-active {
  animation: bounce-in 0.5s reverse;
}
@keyframes bounce-in {
  0% {
    transform: scale(0);
  }
  50% {
    transform: scale(1.5);
  }
  100% {
    transform: scale(1);
  }
}
</style>
