import axiosInstance from '@/api/axios-instance';

export default {
  getCart(accessKey = '') {
    return axiosInstance
      .get('/baskets', {
        params: {
          userAccessKey: accessKey,
        },
      })
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },

  deleteProduct(accessKey, productId) {
    return axiosInstance
      .delete('/baskets/products', {
        params: {
          userAccessKey: accessKey,
        },
        data: {
          basketItemId: productId,
        },
      })
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },

  sendProduct(accessKey, request) {
    return axiosInstance
      .post(
        '/baskets/products',
        {
          ...request,
        },
        {
          params: {
            userAccessKey: accessKey,
          },
        }
      )
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },

  changeProductAmount(accessKey, { amount, id }) {
    return axiosInstance
      .put(
        '/baskets/products',

        {
          basketItemId: id,
          quantity: amount,
        },
        {
          params: {
            userAccessKey: accessKey,
          },
        }
      )
      .then((response) => {
        return response.data;
      })
      .catch((error) => {
        throw error;
      });
  },
};
