<template>
  <button
    class="item__button button button--primery"
    :class="[{ 'button--loading': loading }, arrClasses]"
  >
    {{ buttonText }}
    <BaseSpiner v-if="loading" />
  </button>
</template>

<script>
import BaseSpiner from './BaseSpiner.vue';
export default {
  components: { BaseSpiner },
  props: {
    buttonTextBase: {
      type: String,
      default: 'В корзину',
    },
    buttonTextDone: {
      type: String,
      default: 'Добавлено',
    },
    arrClasses: {
      type: Array,
      default: () => [],
    },
    actionIsDone: {
      type: Boolean,
      default: false,
    },
    loading: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    buttonText() {
      return this.actionIsDone ? this.buttonTextDone : this.buttonTextBase;
    },
  },
};
</script>

<style>
.button {
  position: relative;
}

.button--loading {
  background-color: #150c26;
  cursor: not-allowed;
}
</style>
