<template>
  <select
    class="form__select"
    :name="selectName"
    :value="modelValue"
    @change="sendSelectedValue($event.target.value)"
  >
    <option v-if="isZeroOptionShow" value="0" selected>Все категории</option>
    <option
      v-for="item in list"
      :value="item.id"
      :key="item.id"
      :label="item.title"
    ></option>
  </select>
</template>

<script>
export default {
  props: {
    list: Array,
    selectName: {
      default: '',
    },
    modelValue: [String, Number],
    isZeroOptionShow: {
      default: true,
    },
  },

  methods: {
    sendSelectedValue(value) {
      this.$emit('update:modelValue', Number(value));
    },
  },
};
</script>
